<template>
    <div class="com-upload-mulit">
        <!--  -->
        <label class="btn btn-default" :ref="opts.name">
            {{opts.text}}
            <input name="upload" class="input-upload" type="file" multiple>
        </label>
        <!--  -->
        <ul name="previews" class="previews" v-show="0 < previews.length">
            <li v-for="(preview, i) in previews" :key="preview.id">
                <div v-if="1 == preview.status || undefined == preview.status" class="preview-item preview-item-success">
                    <!--  -->
                    <span v-if="-1 != [100, undefined].indexOf(preview.progress)" @click="remove(i, preview.id)" class="remove fa fa-remove"></span>
                    <span class="mask" :style="{background: 'rgba(0,0,0, ' + (100 - preview.progress) / 100 + ')'}">
                    </span>
                    <p v-if="100 > preview.progress && '' != preview.progress" class="progress2">{{preview.progress}}%</p>
                    <a target="_new" :href="preview.url" class="title">{{preview.fileName}}</a>
                    <img v-if="'image' == preview.type" :src="preview.cover">
                </div>
                <div v-else class="preview-item preview-item-error">
                    <!--  -->
                    <span v-if="-1 != [100, undefined].indexOf(preview.progress)" @click="remove(i, preview.id)" class="remove fa fa-remove"></span>
                    <p></p>
                    <a class="btn-retry" @click="retry(i)"></a>
                </div>
            </li>
        </ul>
    </div>
</template>
<script>
// fileAPI,, 
import FileAPI from 'fileapi'
export default {
    name: 'uploadMulit',

    props: {
        opts: {},

        value: {
            default () {
                return [];
            }
        }
    },

    data() {
        return {
            previews: []
        };
    },

    mounted() {
        this.previews = null == this.opts.value ? [] : JSON.parse(JSON.stringify(this.opts.value));

        if (null == this.value) {
            this.$emit('input', []);
        }

        // 
        FileAPI.event.on(this.$refs[this.opts.name], 'change', (evt) => {
            var files = FileAPI.getFiles(evt);

            // ,
            files.forEach(file => {
                // 
                // ,  
                let preview = {
                    id: '',
                    cover: '', // 
                    progress: 0, // 
                    fileName: file.name, // 
                    type: 'file', // 
                    url: '', // 
                    file: file,
                    status: 1
                };

                // , base64
                if (/^image/.test(file.type)) {
                    preview.type = 'image';

                    // base64, cover
                    this.file2base64(file).then(base64 => {
                        preview.cover = base64;
                        this.upload(file, base64, progress => {
                            preview.progress = progress;
                        }, response => {
                            preview.status = response.status;
                            if (1 == response.status) {
                                preview.url = response.data.url;
                                preview.id = response.data.id;
                            }
                        });
                    })

                } else {
                    this.upload(file, '', progress => {
                        preview.progress = progress;
                    }, response => {
                        preview.status = response.status;
                        if (1 == response.status) {
                            preview.url = response.data.url;
                            preview.id = response.data.id;
                        }
                    });
                }
                this.previews.push(preview);
            });
        });
    },

    destroyed() {
        this.previews = null;
    },

    methods: {
        /**
         * 
         * @param  {Number} index 
         */
        retry(index) {
            var preview = this.previews[index];
            this.upload(preview.file, preview.cover, progress => {
                preview.status = 1;
                preview.progress = progress;
            }, response => {
                preview.status = response.status;
                if (1 == response.status) {
                    preview.url = response.data.url;
                    preview.id = response.data.id;
                }
            });
        },

        /**
         * 
         * filebase64
         * 100px
         * @param  {Object} file      
         */
        file2base64(file) {
            return new Promise((resolve, reject) => {
                FileAPI.Image(file).preview(100).get((err, img) => {
                    if (err) {
                        reject(err);
                    } else {
                        resolve(img.toDataURL());
                    }
                });
            });
        },
        /**
         * 
         * @param  {Object}   file     
         * @param  {String}   cover    
         * @param  {Function} progress 
         * @param  {Function} done     
         */
        upload(file, cover = '', progress = () => {}, done = () => {}) {
            FileAPI.upload({
                url: this.opts.url.upload,
                // 
                headers: { 'Access-Token': this.$store.state.loginModule.accessToken },

                data: {
                    cover,
                    ...this.$route.query
                },

                progress: (evt) => {
                    progress(Math.floor(evt.loaded / evt.total * 100));
                },

                files: {
                    file
                },

                complete: (err, xhr, file, options) => {
                    done(JSON.parse(xhr.response));

                }
            });
        },
        /**
         * 
         * @param  {Number} index 
         * @param  {Number} id    id
         */
        remove(index, id) {
            axios.delete(this.opts.url.del, {
                params: {
                    id
                }
            }).then(response => {
                this.previews.splice(index, 1);
            }).catch((error) => {
                syslog(error);
            });
        }
    },

    watch: {
        previews(value) {
            this.$emit('input', value);
        }
    }
}
</script>
<style scoped lang="scss">
$h: 100px;
.com-upload-mulit {
    overflow: hidden;
    .input-upload {
        display: none;
    }
    ul.previews {
        border-radius: 4px;
        margin: 15px 0;
        padding: 10px;
        overflow-x: auto;
        overflow-y: hidden;
        border: 1px solid #ddd;
        >li {
            transition: all .3s;
            background: #444;
            border-radius: 4px;
            height: $h;
            width: $h;
            display: inline-block;
            vertical-align: top;
            margin: 5px;
            box-shadow: 1px 2px 3px rgba(0, 0, 0, .1);
            .preview-item {
                position: relative;
                width: 100%;
                height: 100%;
                .remove {
                    position: absolute;
                    top: -6px;
                    right: -6px;
                    background: rgba(#e4685f, 1);
                    color: #fff;
                    border-radius: 100%;
                    line-height: 20px;
                    height: 20px;
                    width: 20px;
                    text-align: center;
                    z-index: 100;
                    font-size: 12px;
                    transition: all .2s ease-in-out;
                    &:hover {
                        transform: rotate(90deg);
                        cursor: pointer;
                        background: rgba(#98261d, 1);
                    }
                }
                >.mask {
                    position: absolute;
                    top: 0;
                    left: 0;
                    z-index: 3;
                    width: 100%;
                    height: 100%;
                }
                >img {
                    width: 100%;
                    height: 100%;
                    display: block;
                    position: absolute;
                    top: 0;
                    left: 0;
                    z-index: 2;
                }
                /*boot*/
                >.progress2 {
                    color: #fff;
                    font-size: 14px;
                    position: absolute;
                    bottom: 0;
                    z-index: 3;
                    height: 16px;
                    width: 100%;
                    text-align: center;
                }
                >.title {
                    word-break: break-all;
                    overflow-x: hidden;
                    overflow-y: auto;
                    padding: 20px 5px;
                    color: #fff;
                    font-size: 12px;
                    position: absolute;
                    z-index: 3;
                    height: $h - 16px - 20px;
                    width: 100%;
                    text-align: center;
                    text-shadow: 1px 2px 30px #000;
                    &:hover {
                        cursor: pointer;
                    }
                }
            }
            // 
            .preview-item-success {}
            // 
            .preview-item-error {
                p {
                    padding: 15px 0;
                    color: #ccc;
                    text-align: center;
                    font-size: 16px;
                }
                .btn-retry {
                    margin-top: 15px;
                    color: #fff;
                    text-align: center;
                    display: block;
                    letter-spacing: 1px;
                    &:hover {
                        cursor: pointer;
                    }
                }
            }
        }
    }
}
</style>



