<template>
    <ul class="com-tree">
        <li v-for="(item, i) in data">
            <!--  -->
            <template v-if="undefined != item.children">
                <div class="node" :style="{paddingLeft: level * paddingIndex + 'px'}" @click="toggle(item)">
                    <a>{{item.label}} </a>
                    <i class="fa fa-caret-right" :class="item.open && 'rotate'"></i>
                </div>
                <v-tree :value="value" :level="level + 1" v-show="item.open" :data="item.children" @input="bubble">
                </v-tree>
            </template>
            <!--  -->
            <template v-else>
                <div @click="select(item)" class="node" :style="{paddingLeft: level * paddingIndex + 'px'}">
                    <a :class="{active: value.value == item.value}">{{item.label}} </a>
                </div>
            </template>
        </li>
    </ul>
</template>
<script>
export default {
    name: 'tree',

    beforeCreate: function() {
        this.$options.components.VTree = require('./Tree');
    },

    props: {
        data: {
            type: Array
        },

        value: {

        },

        level: {
            type: Number,
            default () {
                return 0;
            }
        }
    },

    data() {
        return {
            paddingIndex: 30
        };
    },

    methods: {
        toggle(item) {
            item.open = !item.open;
        },

        select(item) {
            this.$emit('input', item);
        },
        /**
         * value
         * @param  {Any} item 
         */
        bubble(item) {
            this.$emit('input', item);
        }
    }
}
</script>
<style scoped lang=scss>
ul.com-tree {
    >li {
        overflow: hidden;
        /*  */
        .node {
            padding: 10px 30px;
            overflow: hidden;
            border-radius: 4px;
            &:hover {
                cursor: pointer;
                background: #ccc;
            }
            >a {
                transition: all .3s;
                padding: 0 5px;
                font-size: 14px;
                line-height: 1.4;
                margin: 0;
                text-decoration: none;
                display: block;
                height: 20px;
                float: left;
                color: #444;
                &.active {
                    color: #69c;
                }
            }
            >i {
                margin-left: 5px;
                transition: all .3s;
                &.rotate {
                    transform: rotate(90deg);
                }
            }
        }
    }
}
</style>



